import React, { useState, useEffect, PureComponent } from "react";
import {
  LocationSearching,
  MailOutline,
  PermIdentity,
  PhoneAndroid,
} from "@material-ui/icons";
import "./css_pages/user.css";
import "./css_pages/switch.css";
import { Link } from "react-router-dom";
import Cookies from "js-cookie";
import { FaEye } from "react-icons/fa";
import OrderService from "../service/Service/OrderService";
import ShipperService from "../service/Service/ShipperService.js";
import { Modal, Button } from "react-bootstrap";
import profileService from "../service/Service/profileService";

export class ShipperDetail extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: {
        address: "",
        avatar: "",
        cmtCode: "",
        district: "",
        emailUser: "",
        idCompany: 1,
        idDistrict: 1,
        idProvince: 1,
        idShipper: 1,
        idUser: 1,
        idWards: 1,
        licensePlates: "",
        name: "",
        nameCompany: "",
        numberPlate: "",
        phone: "",
        province: "",
        status: "",
        wards: "",
        weightCar: 12,
      },
      dataShipper: [],
      idUser: this.props.location.state.data,

      //Thong tin hang hoa
      dataInfo: [],
      order: null,
    };
  }
  componentDidMount() {
    this.getData();
  }
  getStatus(status) {
    switch (status) {
      case 1:
        return <span className="stt1">Shipper va nhn n hng</span>;
      case 2:
        return <span className="stt1">ang ly hng</span>;
      case 3:
        <span className="stt1"> nhn c hng</span>;
      case 4:
        return <span className="stt1">ang giao hng</span>;
      case 5:
        return <span className="stt2"> giao hng thnh cng</span>;
      case 6:
        return <span className="stt3">n hng  hy</span>;
      case 7:
        return <span className="stt4">Giao hng khng thnh cng</span>;
      case 8:
        return <span className="stt1">Lu kho</span>;
    }
  }
  //Ly D Liu
  getData() {
    // let data = {
    //   // idUser: `${window.location.href.slice(-1)}`,
    // idUser: this.props.location.state.data,
    // };
    // profileService
    //   .information(this.state.idUser)
    //   .then((res) => console.log(res.data));
    // let id = this.props.location.state.data;
    ShipperService.getShipperById(this.state.idUser).then((res) => {
      this.setState({
        data: res.data,
      });
    });

    OrderService.getByIdShipper(this.state.idUser).then((res) => {
      this.setState({
        dataInfo: res.data,
      });
      console.log(res.data);
    });
  }

  routeChange(item) {
    let path = `/company/orderDetail`;

    OrderService.getOrderById(item.idInfo).then((res) => {
      console.log(res.data);
      this.props.history.push({
        pathname: path,
        state: { data: res.data },
      });
    });
  }

  render() {
    return (
      <div className="row">
        <div className="col-12">
          <div className="card2">
            <div style={{ width: "300px" }}>
              <h3 className="title">Thng tin chi tit </h3>
            </div>

            <div className="userContainer">
              <div className="userShowLeft">
                <div className="userShowTop">
                  <img
                    className="imageAvatar"
                    src={`data:image/png;base64,${this.state.data.avatar}`}
                    alt="avatar"
                  />
                </div>
                <div className="userShowTopTitle">
                  <span className="userShowUsername">
                    {this.state.data.name}
                  </span>
                  <span className="userShowUserTitle">
                    M ngi dng : {this.state.data.idUser}
                  </span>
                </div>
                <Link
                  to={"/shipperupdate"}
                  style={{ textDecoration: "none", color: "white" }}
                >
                  <div
                    className="userUpdateRight"
                    style={{ textDecoration: "none", color: "white" }}
                  >
                    {this.state.status == 1 ? (
                      <button
                        style={{ textDecoration: "none", color: "white" }}
                        className="userUpdateButtonSS"
                      >
                        Hot ng
                      </button>
                    ) : (
                      <button
                        style={{ textDecoration: "none", color: "white" }}
                        className="userUpdateButtonER"
                      >
                        Ngng hot ng
                      </button>
                    )}
                  </div>
                </Link>
              </div>
              <div className="userShow">
                <span className="userShowTitle">Chi Tit Ti Khon</span>
                <div className="userShowInfo">
                  <PermIdentity className="userShowIcon" />
                  <span className="userShowInfoTitle">
                    {this.state.data.name}
                  </span>
                </div>
                <div className="userShowInfo">
                  <PhoneAndroid className="userShowIcon" />
                  <span className="userShowInfoTitle">
                    {this.state.data.phone}
                  </span>
                </div>
                <div className="userShowInfo">
                  <MailOutline className="userShowIcon" />
                  <span className="userShowInfoTitle">
                    {this.state.data.emailUser}
                  </span>
                </div>
                <div className="userShowInfo">
                  <LocationSearching className="userShowIcon" />
                  <span className="userShowInfoTitle">
                    {this.state.data.address}
                    {", "}
                    {this.state.data.wards}
                    {", "}
                    {this.state.data.district}
                    {", "}
                    {this.state.data.province}
                  </span>
                </div>
              </div>
              <div className="userShow">
                <span className="userShowTitle">Chi Tit Ti X</span>
                <div className="userShowInfo">
                  <span className="userShowUsername">ID : </span>
                  <span className="userShowInfoTitle">
                    {this.state.data.idShipper}
                  </span>
                </div>
                <div className="userShowInfo">
                  <span className="userShowUsername">Cng Ty : </span>
                  <span className="userShowInfoTitle">
                    {this.state.data.nameCompany}
                  </span>
                </div>
                <div className="userShowInfo">
                  <span className="userShowUsername">cmtCode : </span>
                  <span className="userShowInfoTitle">
                    {this.state.data.numberPlate}
                  </span>
                </div>
                <div className="userShowInfo">
                  <span className="userShowUsername">Ti Trng : </span>
                  <span className="userShowInfoTitle">
                    {this.state.data.weightCar}
                  </span>
                </div>
                <div className="userShowInfo2">
                  <span className="userShowUsername">Giy Php : </span>
                  <span className="userShowInfoTitle"> Kim Duyt</span>
                  <Link
                    to={{
                      pathname: "/company/shipper",
                      // state: { data: item },
                    }}
                    style={{
                      textDecoration: "none",
                      color: "black",
                    }}
                    title="Click  xem giy php"
                  >
                    {/* <i> Click  xem nh</i> */}
                    <FaEye size={20} style={{ margin: "10px" }} />
                  </Link>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div className="card">
              <div className="card__header">
                <div style={{ width: "300px" }}>
                  <h5 className="title">Danh sch n hng:</h5>
                </div>
              </div>
              <table>
                <thead>
                  <tr>
                    <th>M n</th>
                    {/* <th>Ngi Gi</th> */}
                    {/* <th>S in Thoi Gi</th> */}
                    {/* <th>Ngi Nhn</th> */}
                    {/* <th>S in Thoi Nhn</th> */}
                    <th>Sn phm vn chuyn</th>
                    <th>Ngy Gi</th>
                    <th>Ngy Nhn</th>
                    <th>Khong cch</th>
                    {/* <th>a Ch Nhn</th> */}
                    {/* <th>a Ch Giao</th> */}
                    <th>Gi Tin</th>
                    <th>Trng Thi</th>
                    <th>Thi gian</th>
                  </tr>
                </thead>
                <tbody>
                  {this.state.dataInfo.map((item) => {
                    return (
                      <tr
                        key={item.idInfo}
                        onClick={() => {
                          this.routeChange(item);
                        }}
                      >
                        <td>
                          <b>#{item.idInfo}</b>
                        </td>
                        <td>{item.informationline.nameProduct}</td>
                        <td>{item.informationline.sendDate}</td>
                        <td>{item.informationline.receivedDate}</td>
                        <td>{item.informationline.kilomet}</td>
                        <td>{item.informationline.phiShip}</td>
                        <td>{this.getStatus(item.status)}</td>
                        <td>{item.time}</td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ShipperDetail;
