import React, { PureComponent, useState } from "react";
import { Button, Row, Col, Container, Form } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import OrderService from "../../../service/Service/OrderService";
import {
  FaCircle,
  FaRegAddressCard,
  FaPhoneAlt,
  FaUser,
  FaRegListAlt,
  FaShippingFast,
  FaCheckCircle,
  FaMapMarkerAlt,
  FaInfoCircle,
} from "react-icons/fa";
import { Link } from "react-router-dom";
import "./detailOrder.css";
export default class OrderDetail extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      data: this.props.location.state.data,
      history: [],
    };
  }
  componentDidMount() {
    OrderService.getHistory(this.state.data.idInfo).then((res) => {
      this.setState({ history: res.data });
      console.log(res.data);
    });
  }
  getStatus(status) {
    switch (status) {
      case 1:
        return <span className="stt1">Shipper va nhn n hng</span>;
      case 2:
        return <span className="stt1">ang ly hng</span>;
      case 3:
        <span className="stt1"> nhn c hng</span>;
      case 4:
        return <span className="stt1">ang giao hng</span>;
      case 5:
        return <span className="stt2"> giao hng thnh cng</span>;
      case 6:
        return <span className="stt3">n hng  hy</span>;
      case 7:
        return <span className="stt4">Giao hng khng thnh cng</span>;
      case 8:
        return <span className="stt1">Lu kho</span>;
    }
  }

  render() {
    let data = this.state.data;
    let history = this.state.history;
    return (
      <>
        <h3 className="title">Thng tin n hng </h3>
        <Container>
          <Row
            className="nvgh"
            style={{
              borderRadius: 10,
              padding: 10,
            }}
          >
            <Col className="m-auto">
              <p className="titleShipper">
                <FaRegAddressCard size={30} style={{ marginRight: "10px" }} />
                Nhn vin giao hng
              </p>
            </Col>
            <Col>
              <div>
                <img
                  className="imgShipper"
                  style={{ borderRadius: 50 }}
                  src={`data:image/png;base64,${data.avtShipper}`}
                  alt="Avatar shipper"
                />
              </div>
            </Col>
            <Col lg={8} md={6} sm={12} style={{ marginTop: "15px" }}>
              <Link
                to={{
                  pathname: "/company/shipperdetail",
                  state: { data: data.idShipper },
                }}
                style={{
                  textDecoration: "none",
                  color: "black",
                }}
                title="Click  xem thng tin shipper"
              >
                <p className="nameNhanvien">{data.nameShipper}</p>
              </Link>

              <p className="sodienthoai">
                <FaPhoneAlt
                  size={20}
                  color={"#B03A2E"}
                  style={{ marginRight: "20px" }}
                />
                {data.phoneShipper}
              </p>
            </Col>
          </Row>
          <p className="hr"></p>
          <Row>
            <div className="ttkh">
              <h5>
                <FaUser size={25} style={{ marginRight: "10px" }} />
                Thng tin khch hng
              </h5>
            </div>
            <Row>
              <Col></Col>
              <Col>
                <i>Thng tin ngi gi</i>
              </Col>
              <Col>
                <i>Thng tin ngi nhn</i>
              </Col>
            </Row>

            <Row>
              <Col>Tn</Col>
              <Col>
                <b>{data.informationline.nameSender}</b>
              </Col>
              <Col>
                <b>{data.informationline.nameReceiver}</b>
              </Col>
            </Row>
            <Row>
              <Col>S in thoi</Col>
              <Col>
                <i style={{ color: `var(--main-color)` }}>
                  {data.informationline.phoneSender}
                </i>
              </Col>
              <Col>
                <i style={{ color: `var(--main-color)` }}>
                  {data.informationline.phoneReceiver}
                </i>
              </Col>
            </Row>

            <div style={{ marginTop: "20px" }}>
              <b style={{ fontSize: 14 }}>
                <i>
                  <FaInfoCircle
                    size={15}
                    style={{ marginRight: "10px" }}
                  ></FaInfoCircle>
                  Ghi ch ca khch hng
                </i>
              </b>
              <div className="note">
                <p style={{ fontSize: 14 }}>{data.informationline.note}</p>
              </div>
            </div>
          </Row>
          <p className="hr"></p>
          <Row>
            <div className="ttkh">
              <h5>
                <FaRegListAlt size={25} style={{ marginRight: "10px" }} />
                Thng tin chi tit n hng
              </h5>
            </div>
            <Row>
              <Col xs={4}>
                <div>
                  <img
                    // className="avata"
                    width="300px"
                    // height="150px"
                    alt="product"
                    style={{
                      borderRadius: "10px",
                      marginLeft: "10px",
                      marginBottom: "10px",
                    }}
                    src={`data:image/png;base64,${data.informationline.img}`}
                  />
                </div>
              </Col>
              <Col lg={7} md={6} sm={12}>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Tn sn phm:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b className="nameProduct">
                        {data.informationline.nameProduct}
                      </b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Khi lng:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.weightProduct}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>S lng:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.numbersProduct}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>a ch ly hng:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.addressSender}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>a ch giao hng:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.addressReceiver}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Thi gian nhn hng d kin:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.sendDate}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Thi gian giao hng d kin:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.receivedDate}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Khong cch:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.kilomet}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Thu h:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.priceProduct}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <FaCircle
                        size={9}
                        style={{
                          marginRight: "20px",
                        }}
                      />
                      <i>Ph ship:</i>
                    </div>
                  </Col>
                  <Col>
                    <div>
                      <b>{data.informationline.phiShip}</b>
                    </div>
                  </Col>
                </Row>
                <Row>
                  <Col>
                    <div>
                      <p className="stt">
                        <FaCheckCircle
                          color={`var(--second-color)`}
                          style={{ marginRight: "10px" }}
                        />
                        Trng thi n hng hin ti:
                      </p>
                    </div>
                  </Col>
                  <Col>
                    <div className="stt">
                      {this.getStatus(this.state.data.status)}
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Row>
          <Row>
            <div className="ttkh">
              <h5>
                <FaShippingFast size={25} style={{ marginRight: "10px" }} />
                Thng tin vn chuyn chi tit
              </h5>
            </div>
          </Row>

          <Container>
            {this.state.history.map((item, index) => {
              return (
                <div>
                  <li>
                    <span className="label-time">
                      <i>{item.time}:</i>

                      {this.getStatus(item.status)}
                    </span>

                    <div className="addressStt">
                      {" "}
                      <FaMapMarkerAlt
                        size={14}
                        style={{ marginRight: "5px" }}
                      />
                      {item.address}
                    </div>
                  </li>
                  <div className="sep"></div>
                </div>
              );
            })}
          </Container>
        </Container>
      </>
    );
  }
}
