import React, { useState } from "react";
import { Button, Container, Col, Form, Row } from "react-bootstrap";
import anhbiasignup from "../../assets/images/anhbia.png";

import "bootstrap/dist/css/bootstrap.min.css";
//import blue from "../assets/images/blue.png";
//import BlueOther from "../assets/images/orderblue.png";
import "./SignUp.css";

import Alert from "@material-ui/lab/Alert";

//import CompanyApi from "../service/Service/Authentication";

const SignUp = () => {
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [tax, setTax] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [passwordComfirm, setPasswordComfirm] = useState("");
  const [error, setError] = useState("");

  const handleRegister = async (e) => {
    const re =
      /(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))/;
    const pass = /^(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{8,50}$/;
    const phoneNum =
      /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/;
    if ((email || name || tax || phone || password || passwordComfirm) === "") {
      setError("Vui lng khng  chng");
    } else if (email === "") {
      setError("Vui lng nhp Email");
    } else if (name === "") {
      setError("Xin vui lng nhp tn");
    } else if (tax === "") {
      setError("Xin vui lng nhp M S Thu");
    } else if (phone === "") {
      setError("Xin vui lng nhp S in Thoi");
    } else if (phoneNum.test(phone) === false) {
      setError("nh dng s in thoi sai");
    } else if (password === "") {
      setError("Xin vui lng nhp mt khu");
    } else if (passwordComfirm === "") {
      setError("Xin vui lng nhp mt khu Xc Thc");
    } else if (email.length > 100) {
      setError("Email phi m bo:  di khng qu 100 k t");
    } else if (re.test(email) === false) {
      setError("nh dng sai Emai! - v d: nguyenvana@gmail.com");
    } else if (pass.test(password) === false) {
      setError(
        "Mt khu phi: di 8-50 k t, c t nht 1 ch ci (a-z hoc A-Z) v 1 s v c 1 k t c bit"
      );
    } else if (pass.test(passwordComfirm) === false) {
      setError(
        "Xc nhn Mt khu phi: di 8-50 k t, c t nht 1 ch ci (a-z hoc A-Z) v 1 s v c 1 k t c bit"
      );
    } else if (password !== passwordComfirm) {
      setError("Mt khu khng khp");
    }
    const data = {
      name: name,
      emailUser: email,
      phone: phone,
      masothue: tax,
      passwordUser: password,
      status: 1,
      idRole: 2,
      idWards: 4,
    };
    console.log(data);
    //await CompanyApi.registerCompany(data);
    console.log("regiss nek");
  };
  return (
    <>
      <div className="header">ng k - Logistics</div>
      <Row>
        <Container className="col-md-3 mx-auto">
          <Form>
            <Form.Group className="mb-3" controlId="formBasicAccout">
              {error && <Alert severity="error">{error}</Alert>}
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicAccout">
              <Form.Control
                type="text"
                placeholder="Tn cng ty"
                onChange={(e) => setName(e.target.value)}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Control
                type="email"
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Control
                type="text"
                placeholder="M s thu"
                onChange={(e) => setTax(e.target.value)}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Control
                type="number"
                placeholder="S in thoi"
                onChange={(e) => setPhone(e.target.value)}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Control
                type="password"
                placeholder="Mt khu"
                onChange={(e) => setPassword(e.target.value)}
              />
              <Form.Text className="text-muted"></Form.Text>
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Control
                type="password"
                placeholder="Xc nhn mt khu"
                onChange={(e) => setPasswordComfirm(e.target.value)}
              />
            </Form.Group>
            <div className="text-left">
              <Form.Check
                className="reset1 ml-2"
                type="checkbox"
                label="Nh mt khu"
              />
            </div>
            <Button
              className="primary"
              variant="primary"
              size="lg"
              onClick={handleRegister}
            >
              ng k
            </Button>
            {/* <hr className='hrtag' /> */}
          </Form>
        </Container>
        <Col lg={6} md={6} sm={12}>
          <img className="imgBia" alt="" src={anhbiasignup} />
        </Col>
      </Row>
      {/* <img className="nav-blue" src={BlueOther} alt="" />
      <img className="nav-last" src={blue} alt="" /> */}
    </>
  );
};

export default SignUp;
