import React, { useState } from "react";
import "./changePassword.css";
import Authentication from "../../service/Service/Authentication";
import Cookies from "js-cookie";
import { Alert } from "react-bootstrap";

const ChangePassword = (props) => {
  const [confirm, setConfirm] = useState("");
  const [oldpassword, setOldpassword] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);
  const [Suc, setSuc] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [showAlertSuc, setShowAlertSuc] = useState(false);

  const changePass = async () => {
    const idUser = Cookies.get("id");
    if (password.length <= 8 || password.length >= 20) {
      setError("Mt khu ln hn 8 v b hn 20 k t !");
    } else if (password !== confirm) {
      setError("Mt khu khng khp. Vui lng nhp li !");
    } else {
      await Authentication.changePass(idUser, oldpassword, password).then(
        (res) => {
          if (!res.data) {
            setError("Mt khu khng ng. Vui lng kim tra li !");
          } else {
            setSuc("Thay i mt khu thnh cng !");
            setError("");
          }
        }
      );
    }
  };

  return (
    <div className="row">
      <div className="col-12">
        <div className="card">
          <h2 className="page-header">Thay i mt khu</h2>
          <div className="userContainer">
            <div className="userShowLeft">
              <div className="userShowTop">
                <span className="userShowUserTitle">Cc lu  : </span>
              </div>
              <span className="userShowUserTitle">
                - Mt khu bng hoc nhiu hn 8 k t{" "}
              </span>
              <div className="userShowTop">
                <span className="userShowUserTitle">
                  - Mt khu bng hoc t hn 20 k t{" "}
                </span>
              </div>
            </div>
            <div className="userShow">
              <div className="userUpdateLeft">
                <div className="userUpdateItem">
                  <label>Mt khu c</label>
                  <input
                    type="password"
                    placeholder=". . . . . . . ."
                    className="userUpdateInput"
                    onChange={(e) => setOldpassword(e.target.value)}
                  />
                </div>
                <div className="userUpdateItem">
                  <label>Mt khu mi</label>
                  <input
                    type="password"
                    placeholder=". . . . . . . ."
                    className="userUpdateInput"
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>
                <div className="userUpdateItem">
                  <label>Xc nhn mt khu</label>
                  <input
                    type="password"
                    placeholder=". . . . . . . ."
                    className="userUpdateInput"
                    onChange={(e) => setConfirm(e.target.value)}
                  />
                </div>
                <div>
                  <h6 className="error">{error}</h6>
                  <h6 className="Suc">{Suc}</h6>
                </div>
              </div>
            </div>
          </div>
          <div className="userUpdateRight">
            <button className="userUpdateButton" onClick={changePass}>
              Xc Nhn
            </button>
          </div>
          {showAlert && (
            <Alert className="alert-fail" variant="danger">
              Thay i mt khu tht bi. Vui lng th li
            </Alert>
          )}
          {showAlertSuc && (
            <Alert className="alert-fail" variant="success">
              Thay i mt khu thnh cng !
            </Alert>
          )}
        </div>
      </div>
    </div>
  );
};

export default ChangePassword;
